/* !checkout popup item */

.checkout__popup-data {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  padding: 15px;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  visibility: hidden;
}

.checkout__sign {
  border-bottom: 1px solid red;
}

.checkout__popup-open {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease-in-out;
}

.checkout__data-content {
  background-color: #ffffff;
  padding: 30px;
  border-radius: 30px;
}
