.custom__checkbox {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border: 2px solid #212529;
  border-radius: 4px;
  vertical-align: sub;
  margin-right: 5px;
}

.real__checkbox {
  width: 0;
  height: 0;
  opacity: 0;
  visibility: 0;
  position: absolute;
  z-index: -1;
}

.real__checkbox:focus + .custom__checkbox {
  box-shadow: 0px 0px 0px 1px #47525e, 0px 0px 10px #46607a;
}

.custom__checkbox::before {
  content: "";
  display: inline-block;
  width: 13px;
  height: 13px;
  background-color: #212529;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.2s ease-in;
}

.real__checkbox:checked + .custom__checkbox::before {
  transform: translate(-50%, -50%) scale(1);
}
